@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  background-color: #e7e5e5;
}
.page__container {
  display: grid;
  width: 100%;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  grid-template-rows: min-content 1fr min-content;
}
.page__map {
  margin-bottom: 8px;
}
.main-header {
  position: relative;
  min-height: 56px;
}
.main-header__logo {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  width: 185px;
  margin: auto;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.4;
}
.main-header__logo img {
  max-height: 96px;
}
.main-header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  width: 63px;
  height: 56px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.is-open .main-header__toggle::before,
.is-open .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 25px;
  right: 20px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.is-open .main-header__toggle::before {
  box-shadow: none;
  transform: rotate(45deg);
}
.is-open .main-header__toggle::after {
  transform: rotate(-45deg);
}
.no-js .main-header__toggle {
  display: none;
}
.main-header__nav {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  display: none;
}
@media (min-width: 768px) {
  .main-header__nav {
    position: static;
    z-index: 0;
    display: block;
  }
}
.is-open .main-header__nav {
  display: block;
}
.no-js .main-header__nav {
  position: relative;
  display: block;
}
.main-nav__list {
  position: relative;
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767.98px) {
  .main-nav__list {
    background-color: #81b3d2;
    gap: 1px;
  }
  .main-nav__list::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 7px;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    width: 100%;
    grid-template-columns: 1fr 1fr minmax(153px, 1fr) 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(4n + 3) {
    grid-column-start: 4;
  }
}
.main-nav__item:last-child .main-nav__link {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-nav__link {
  display: block;
  padding: 18px 60px 11px 60px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (max-width: 767.98px) {
  .main-nav__link {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    position: relative;
    z-index: 1;
    padding: 18px 17px 17px 17px;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: transparent;
  }
}
@media (max-width: 767.98px) {
  .main-nav__link[href]:hover {
    background-color: #669ec0;
  }
}
@media (min-width: 768px) {
  .main-nav__link[href]:hover {
    color: #81b3d2;
  }
}
@media (max-width: 767.98px) {
  .main-nav__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .main-nav__link[href]:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__link--current {
    color: #766357;
  }
}
