.custom-element {
  display: grid;
  align-items: start;
  cursor: pointer;
  gap: 16px;
  grid-template-columns: 46px 1fr;
}
.custom-element:hover .custom-element__icon {
  background-color: #e5e5e5;
}
.custom-element__icon {
  position: relative;
  display: block;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
}
.custom-element__icon--radio {
  border-radius: 50%;
}
.custom-element__icon--radio::before {
  width: 16px;
  height: 16px;
}
.custom-element__icon::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 22px 16px;
  border-radius: 50%;
}
.custom-element__icon--checkbox {
  border-radius: 4px;
}
.custom-element__input:checked + .custom-element__icon--radio::before {
  background-color: #333333;
}
.custom-element__input:checked + .custom-element__icon--checkbox::before {
  background-image: url("../img/stack.svg#icon-tick");
}
.custom-element__input:active + .custom-element__icon {
  background-color: #d5d5d5;
}
.custom-element__input:active + .custom-element__icon::before {
  opacity: 0.3;
}
.custom-element__input:disabled ~ span {
  cursor: auto;
  opacity: 0.3;
}
.custom-element__input:disabled + .custom-element__icon {
  background-color: #f2f2f2;
}
.custom-element__input:disabled + .custom-element__icon::before {
  opacity: 1;
}
.custom-element__input:focus + .custom-element__icon {
  box-shadow: 0 0 0 2px #81b3d2;
}
.custom-element__text {
  margin-top: 11px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: none;
}
